<template>
  <div class="q-radio-box">
    <span class="q-radio"
          v-for="radio in list"
          @click="setRadio(radio)"
          :class="[{'is-checked': radio.id==curId, 'is-disabled': radio.disabled}]">
      <span class="q-radio__check"></span>
      <span class="q-radio__label" v-bind:class="[radio.id==curId ? '' : 'ys-info-color' ]" style="margin-left:3px;">{{radio.text}}</span>
    </span>
  </div>
</template>
<script>
  export default {
    name: 'QRadio',
    props: {
      list: {
        type: Array,
        default: []
      },
      value: {
        default: '',
      },
      disabled: Boolean
    },
    data () {
      return {
        curId: 0,
      }
    },
    mounted(){
      this.curId=this.value;
    },
    watch: {
    },
    methods: {
      setRadio (radio) {
        if(!radio.disabled){
          this.curId = radio.id;
          this.$emit('input',this.curId)
        }
      }
    }
  }
</script>


