<template>
  <div class="ys-capsule-box" :class="[ typeClass ]">
    <span class="title">{{title}}</span>
    <span class="number">{{num}}</span>
  </div>
</template>
<script>
  export default {
    name: 'QCapsule',
    props: {
      type: {
        type: String,
        default: "info"
      },
      title: {
        type: String,
        default: ''
      },
      num: {
        type: String,
        default: ''
      },
    },
    computed: {
      typeClass() {
        return this.type;
      }
    },
    mounted(){

    }
  }
</script>
<style scoped>
.ys-capsule-box{
  display: inline-block;
  width:120px;
  height:24px;
  border:1px solid rgba(74,146,255,0.3);
  border-radius: 20px;
  overflow: hidden;
  vertical-align: middle;
  margin-right:5px;
}
.ys-capsule-box>span{
  width:50%;
  height:24px;
  line-height:24px;
  display: inline-block;
  text-align:center;
  float:left;
}
.ys-capsule-box .title{
  background:rgba(74,146,255,0.3);
  color:#4a92ff;
}
.ys-capsule-box .name{
  
}
.ys-capsule-box.warn{
  border:1px solid rgba(218,187,97,0.3);
}
.ys-capsule-box.warn .title{
  background:rgba(218,187,97,0.1);
  color:#dabb61;
}
.ys-capsule-box.success{
  border:1px solid rgba(0,189,133,0.3);
}
.ys-capsule-box.success .title{
  background:rgba(0,189,133,0.1);
  color:#00bd85;
}
.ys-capsule-box.error{
  border:1px solid rgba(233,97,87,0.3);
}
.ys-capsule-box.error .title{
  background:rgba(233,97,87,0.1);
  color:rgba(233,97,87,1);
}
</style>


