<template>
  <span class="ys-tab-box">
    <span v-for="item in list" class="ys-tab-item"
    v-bind:class="[checked==item.id ? 'on' : '']"
    @click="selectOption(item.id)">{{item.name}}</span>
  </span>
</template>
<script>
  export default {
    name: 'QTab',
    props: {
      disabled: {
        type: Boolean,
        default: false
      },
      size: {
        default:"large",
      },
      list:{
        default: []
      },
      value: {
        default: ''
      }
    },
    data () {
      return {
        checked:"",
      }
    },
    mounted() {
      this.checked=this.value;
    },
    methods: {
      selectOption(id){
        this.checked=id;
        this.$emit('input', id)
        this.$emit('change', id)
      }
    }
  };
</script>
<style scoped>
.ys-tab-box{
  display: inline-block;
  background:rgba(0,0,0,0.3);
  height:22px;
  padding:2px;
  border-radius: 3px;
  color:#93a6d8;
  line-height:normal!important;
}
.ys-tab-item{
  cursor: pointer;
  display: inline-block;
  padding:0 14px;
  height:22px;
  line-height:22px;
}
.ys-tab-item.on{
  background:#4a92ff;
  color:#fff;
  border-radius: 3px;
}
</style>


