<template>
  <span class="q-checkbox" 
  :class="[{'is-checked': checked, 'is-disabled': disabled}]" 
  @click="selfClick()">
    <span class="q-checkbox__check">
      <i class="ys-icon icon-check" v-if="checked"></i>
    </span>
    <span v-if="text!=''" class="q-checkbox__label">{{text}}</span>
  </span>
</template>
<script>
  export default {
    name: 'QCheckbox',
    props: {
      value: {
        default:false
      },
      text:{
        default:""
      },
      // 由组件控制开关， 默认为是， 否则有上层控制
      autoSet: {
        default: true
      },
      read:{
        default: false
      },
      disabled: Boolean,
    },
    data(){
      return {
        checked:""
      }
    },
    mounted(){
      this.checked=this.value;
    },
    
    methods: {
      selfClick () {
        if(this.read || this.disabled){return false}
        if (this.autoSet) {
          this.checked = !this.checked
        }
        this.$emit('click', this.checked)
        this.$emit('input', this.checked)
      },
      close() {
        this.checked = false
      }
    }
  }
</script>


