<template>
  <div class="q-input" :class="[{'is-disabled': disabled}]">
    <input class="q-input__inner"
    type="input"
    :value="curInput"
    :disabled="disabled" 
    :readonly="readonly" 
    :placeholder="placeholder"
    @click="handleClick"
    @blur="handleBlur"
    @keydown="handleKeydown"
    @input="handleInput"/>
    <span class="q-input__icon"><slot name="icon"></slot></span>
  </div>  
</template>
<script>
  export default {
    name: 'QInput',
    props: {
      value: {
        default: '',
      },
      placeholder: {
        default: '',
      },
      icon: {
        default: '',
      },
      disabled: {
        type: Boolean,
        default: false
      },
      readonly: {
        type: Boolean,
        default: false
      },
    },
    data () {
      return {
        curInput: this.value === undefined || this.value === null ? '' : this.value,
      }
    },
    watch: {
      value(val, oldValue) {
        this.curInput=val;
      }
    },
    methods: {
      handleInput(event) {
        const value = event.target.value;
        this.curInput=value;
        this.$emit('input', value);
      },
      handleBlur(event) {
        this.$emit('blur', event);
      },
      handleKeydown(event){
        if(event.keyCode==13){this.$emit('keydown', event)}
      },
      handleClick(event){
        this.$emit('click', event);
      },
    }
  };
</script>



