<template>
  <button
    @click="$emit('click')"
    class="q-btn"
    :disabled="disabled"
    :class="[
    'q-btn--'+type,
    {'is-plain': plain, 'is-disabled': disabled, 'is-round': round},
    'q-btn--size-'+size
    ]"><i v-if="icon !== ''" :class="icon"></i><span v-if="$slots.default"><slot></slot></span></button>
</template>
<script>
  export default{
    name: 'QButton',
    props: {
      type: {
        type: String,
        default: 'default'
      },
      size: {
        type: String,
        default: 'default'
      },
      icon: {
        type: String,
        default: ''
      },
      plain: Boolean,
      disabled: Boolean,
      round: Boolean
    },
    data () {
      return {
        msg: 'button'
      }
    }
  }
</script>



