<template>
  <div class="q-input" :class="[{'is-disabled': disabled,'focus': focus}]">
    <input class="q-input__inner"
    type="input"
    :value="curInput"
    :disabled="disabled" 
    :readonly="readonly" 
    :placeholder="placeholder"
    @click="handleClick"
    @blur="handleBlur"
    @keydown="handleKeydown"
    @input="handleInput"
    @mousedown="show=true"/>
    <a class="ys-input-up" v-if="show" @click="up()"><i class="ys-icon icon-arrow-up" @click="preventDefault"></i></a>
    <a class="ys-input-down" v-if="show" @click="down()"><i class="ys-icon icon-arrow-down" @click="preventDefault"></i></a>
  </div>  
</template>
<style scoped>
  .q-input{
    position:relative
  }
  .q-input.focus .q-input__inner{
    border-color: rgba(74,146,255,.7);
    box-shadow: 0 0 7px #4a92ff;
  }
  .q-input .ys-input-up{
    position:absolute;
    right:1px;
    top:1px;
    border-top-right-radius:2px;
  }
  .q-input .ys-input-up i{
    margin-top:1px;
  }
  .q-input .ys-input-down i{
    margin-top:1px;
  }
  .q-input>a{
    display: inline-block;
    width:16px;
    height:12px;
    line-height:0px;
    text-align:center;
    background:rgba(74,146,255,0.3);
    cursor: pointer;
  }
  .q-input>a:hover{
    background:rgba(74,146,255,0.8);
    color:#fff;

  }
  .q-input .ys-input-down{
    position:absolute;
    right:1px;
    bottom:1px;
    border-bottom-right-radius:2px;
  }
</style>
<script>
  export default {
    name: 'QInputNumber',
    props: {
      value: {
        default: '',
      },
      placeholder: {
        default: '',
      },
      icon: {
        default: '',
      },
      min: {
        default: false
      },
      max: {
        default: false
      },
      disabled: {
        type: Boolean,
        default: false
      },
      readonly: {
        type: Boolean,
        default: false
      },
    },
    data () {
      return {
        curInput: this.value === undefined || this.value === null ? '' : this.value,
        show:false,
        focus:false,
      }
    },
    watch: {
      value(val, oldValue) {
        this.curInput=val;
      }
    },
    methods: {
      preventDefault (e) {
        e.preventDefault();
      },
      up(){
        if(this.max && this.curInput==this.max){
          return false
        }else{
          this.curInput++
          this.$emit('input', this.curInput);
        }
      },
      down(){
        if(this.min && this.curInput==this.min){
          return false
        }else{
          this.curInput--
          this.$emit('input', this.curInput);
        }
      },
      handleInput(event) {
        const value = event.target.value;
        this.curInput=value;
        this.$emit('input', value);
      },
      handleBlur(event) {
        this.focus = false;
        this.$emit('blur', event);
      },
      handleKeydown(event){
        if(event.keyCode==13){this.$emit('keydown', event)}
        if(event.keyCode==38){
          this.up()
        }
        if(event.keyCode==40){
          this.down()
        }
      },
      handleClick(event){
        this.focus=true
        this.$emit('click', event);
      },
    }
  };
</script>




