<template>
  <div class="pos-r" v-bind:style="{width: width, height:height+'px'}">
    <div v-show="status==0"
         class="textC"
         v-bind:style="{width: width, height:height+'px',lineHeight:height+'px'}">
      <!-- <loading-normal :show="true"></loading-normal> -->
    </div>
    <div v-show="status==1"
         class="textC ys-info-color"
         v-bind:style="{width: width, height:height+'px',lineHeight:height+'px'}">
      <div class="d-i-b verticalM">
        <p style="line-height:1"><i class="ys-icon icon-chart-no-data" style="margin-left:6px;color:rgba(154,188,238,0.25);font-size: 60px;"></i></p>
        <p style="line-height:1;color:rgba(154,188,238,0.7);" class="m-t-20">暂时没有数据</p>
      </div>
      <span style="display:none;">{{status}}</span>
    </div>
    <div v-show="status==2">
      <div v-bind:style="{width: width,height:height+'px'}">
        <div v-bind:id="'map-chart-' + id"
             v-bind:style="{width: width,height:height+'px'}"
             v-on:resize="handleResize($event)">
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped></style>
<script>
  import 'echarts/lib/chart/map'
  import 'echarts/lib/component/tooltip'
  let echarts = require('echarts/lib/echarts');
  let chinaJson = require("better-echarts-maps/dist/china.js")
  let provinces = require("better-echarts-maps/dist/china-provinces.js")
  let cityCoordinate = require("better-echarts-maps/dist/china-cities-coordinate.js")
  export default {
    name: 'QMapChart',
    props: {
      id: {
        default: 0
      },
      name:{
        default: ""
      },
      width:{
        default: "100%"
      },
      height:{
        default: "200px"
      },
      type:{
        default:"beijing"
      }
    },
    data(){
      return {
        status:2,
        chart: "",
      }
    },
    mounted(){
      this.initChart()
    },
    methods:{
      initChart(){
        let self=this;
        let repeat=setInterval(function(){
          if(document.getElementById('map-chart-'+self.id)){
            self.setChart();
            window.addEventListener('resize', self.handleResize)
            var EleResize = {
              _handleResize: function (e) {
                var ele = e.target || e.srcElement;
                var trigger = ele.__resizeTrigger__;
                if (trigger) {
                  var handlers = trigger.__z_resizeListeners;
                  if (handlers) {
                    var size = handlers.length;
                    for (var i = 0; i < size; i++) {
                      var h = handlers[i];
                      var handler = h.handler;
                      var context = h.context;
                      handler.apply(context, [e]);
                    }
                  }
                }
              },
              _removeHandler: function (ele, handler, context) {
                var handlers = ele.__z_resizeListeners;
                if (handlers) {
                  var size = handlers.length;
                  for (var i = 0; i < size; i++) {
                    var h = handlers[i];
                    if (h.handler === handler && h.context === context) {
                      handlers.splice(i, 1);
                      return;
                    }
                  }
                }
              },
              _createResizeTrigger: function (ele) {
                var obj = document.createElement('object');
                obj.setAttribute('style',
                    'display: block; position: absolute; top: 0; left: 0; height: 100%; width: 100%; overflow: hidden;opacity: 0; pointer-events: none; z-index: -1;');
                obj.onload = EleResize._handleObjectLoad;
                obj.type = 'text/html';
                ele.appendChild(obj);
                obj.data = 'about:blank';
                return obj;
              },
              _handleObjectLoad: function (evt) {
                this.contentDocument.defaultView.__resizeTrigger__ = this.__resizeElement__;
                this.contentDocument.defaultView.addEventListener('resize', EleResize._handleResize);
              }
            };
            if (document.attachEvent) {//ie9-10
              EleResize.on = function (ele, handler, context) {
                var handlers = ele.__z_resizeListeners;
                if (!handlers) {
                  handlers = [];
                  ele.__z_resizeListeners = handlers;
                  ele.__resizeTrigger__ = ele;
                  ele.attachEvent('onresize', EleResize._handleResize);
                }
                handlers.push({
                  handler: handler,
                  context: context
                });
              };
              EleResize.off = function (ele, handler, context) {
                var handlers = ele.__z_resizeListeners;
                if (handlers) {
                  EleResize._removeHandler(ele, handler, context);
                  if (handlers.length === 0) {
                    ele.detachEvent('onresize', EleResize._handleResize);
                    delete  ele.__z_resizeListeners;
                  }
                }
              }
            } else {
              EleResize.on = function (ele, handler, context) {
                var handlers = ele.__z_resizeListeners;
                if (!handlers) {
                  handlers = [];
                  ele.__z_resizeListeners = handlers;

                  if (getComputedStyle(ele, null).position === 'static') {
                    ele.style.position = 'relative';
                  }
                  var obj = EleResize._createResizeTrigger(ele);
                  ele.__resizeTrigger__ = obj;
                  obj.__resizeElement__ = ele;
                }
                handlers.push({
                  handler: handler,
                  context: context
                });
              };
              EleResize.off = function (ele, handler, context) {
                var handlers = ele.__z_resizeListeners;
                if (handlers) {
                  EleResize._removeHandler(ele, handler, context);
                  if (handlers.length === 0) {
                    var trigger = ele.__resizeTrigger__;
                    if (trigger) {
                      trigger.contentDocument.defaultView.removeEventListener('resize', EleResize._handleResize);
                      ele.removeChild(trigger);
                      delete ele.__resizeTrigger__;
                    }
                    delete  ele.__z_resizeListeners;
                  }
                }
              }
            }
            var resizeDiv = document.getElementById('map-chart-'+self.id);
            EleResize.on(resizeDiv, function(){self.handleResize()});
            clearInterval(repeat)
          }
        },300)
      },
      setChart(){
        let self=this
        self.chart = echarts.init(document.getElementById('map-chart-'+self.id));
        let seriesData = [{name:"111",value:222},{name:"222",value:2333322}];
        let curMapData = {};
        if(self.type=="china"){
          echarts.registerMap('china', chinaJson.China[0][1]);
        }else{
          for (let x in provinces.ChinaProvinces) {
            //console.log(provinces.ChinaProvinces[x])
            if (provinces.ChinaProvinces[x][0] == self.type) {
              curMapData = provinces.ChinaProvinces[x][1]
            }
          }
          echarts.registerMap(self.type, curMapData);
        }
        let myChartOption = {
          tooltip: {
            trigger: 'item',
            textStyle: {
              fontSize: 12
            }
          },
          legend: {
            show: false
          },
          toolbox: {
            show: false,
            orient: 'vertical',
            x: 'right',
            y: 'center',
            feature: {
              mark: {show: true},
              dataView: {
                show: true,
                readOnly: false
              },
              restore: {show: true},
              saveAsImage: {show: true}
            }
          },
          series: [{
            name: '访问请求数',//访问请求数
            type: 'map',
            zoom: 1.2,
            mapType: self.type,
            showLegendSymbol: false,
            roam: false,
            itemStyle:{
              normal:{
                areaColor:'rgba(0,0,0,0.2)',
                borderColor:'rgba(74,146,255,0.5)',//shadowColor:'rgba(74,146, 255, 1)',
                //shadowBlur:10
              },
              emphasis:{
                areaColor:'rgba(74,146,255,0.25)'
              }
            },
            label:{
              normal:{
                show:false
              },
              emphasis:{
                show:false,
                color:"#fff",
                fontSize:14,
              }
            },
            data: seriesData
          }]
        };
        self.chart.setOption(myChartOption);
      },
      changeUnit(val){
        if(val<1024){
          return  val + "B"
        }else if(val>=1024 && val<1024*1024){
          val=(val/1024).toFixed(1);
          return  val + "KB"
        }else if(val>=1024*1024 && val<1024*1024*1024){
          val=(val/(1024*1024)).toFixed(1);
          return  val + "MB"
        }else if(val>=1024*1024*1024){
          val=(val/(1024*1024*1024)).toFixed(1);
          return  val + "GB"
        }
      },
      changePacketUnit(val){
        if(val<1000){
          return  val + "pps"
        }else if(val>=1000 ){
          val=(val/1000).toFixed(1);
          return  val + "kpps"
        }  
      },
      changeWanUnit(val){
        if(val<10000){
          return val+"条"
        }else{
          val = (val / 10000).toFixed(2);
          return val+"万条"
        }        
      },
      handleResize(){
        this.chart.resize();
      },
      hexToRgba(hex, al){
        var hexColor = /^#/.test(hex) ? hex.slice(1) : hex, alp = hex === 'transparent' ? 0 : Math.ceil(al), r, g, b; hexColor = /^[0-9a-f]{3}|[0-9a-f]{6}$/i.test(hexColor) ? hexColor : 'fffff'; if (hexColor.length === 3) { hexColor = hexColor.replace(/(\w)(\w)(\w)/gi, '$1$1$2$2$3$3'); } r = hexColor.slice(0, 2); g = hexColor.slice(2, 4); b = hexColor.slice(4, 6); r = parseInt(r, 16); g = parseInt(g, 16); b = parseInt(b, 16); return { hex: '#' + hexColor, alpha: alp, rgba: 'rgba(' + r + ', ' + g + ', ' + b + ', ' + (alp / 100).toFixed(2) + ')' };
      },
      changeStatus(){
        // 0 加载之前   1 加载后无数据   2 加载后有数据
        if(this.x===""){
          this.status = 0;
        }else{
          if(this.x.length>0){
            this.initChart();
            this.status = 2;
          }else{
            this.status = 1;
          }
        }
      }
    },
    watch: {
      type(){
        this.setChart()
      },
      hexToRgba(hex, al){
        var hexColor = /^#/.test(hex) ? hex.slice(1) : hex, alp = hex === 'transparent' ? 0 : Math.ceil(al), r, g, b; hexColor = /^[0-9a-f]{3}|[0-9a-f]{6}$/i.test(hexColor) ? hexColor : 'fffff'; if (hexColor.length === 3) { hexColor = hexColor.replace(/(\w)(\w)(\w)/gi, '$1$1$2$2$3$3'); } r = hexColor.slice(0, 2); g = hexColor.slice(2, 4); b = hexColor.slice(4, 6); r = parseInt(r, 16); g = parseInt(g, 16); b = parseInt(b, 16); return { hex: '#' + hexColor, alpha: alp, rgba: 'rgba(' + r + ', ' + g + ', ' + b + ', ' + (alp / 100).toFixed(2) + ')' };
      }
    }
  }
</script>


