import QButton from './button/index.js'
import WRow from './row/index'
import WCol from './col/index'
import WTag from './tag/index'
import WShowMore from './show-more/index'
import WLimitTextArea from './limit-textarea/index'
import MetaInfo from './meta-info/index'
import WAlert from './alert/index'
import QBox from './box/index'
import QInput from './input/index'
import QInputNumber from './input-number/index'
import QCheckbox from './checkbox/index'
import QRadio from './radio/index'
import QPercent from './percent/index'
import QGuide from './guide/index'
import QCapsule from './capsule/index'
import QModal from './modal/index'
import QSwitch from './switch/index'
import QCalendar from './calendar/index'
import QTooltip from './tooltip/index'
import QPoptip from './poptip/index'
import QSelect from './select/index'
import QAside from './aside/index'
import QTab from './tab/index'
import QTransfer from './transfer/index'
import QLineChart from './line-chart/index'
import QPieChart from './pie-chart/index'
import QMapChart from './map-chart/index'
import QLingBarChart from './ling-bar-chart/index'
import QFengBarChart from './feng-bar-chart/index'
import QRingChart from './ring-chart/index'
import QRadarChart from './radar-chart/index'
import QSlider from './slider/index'
import QTagator from './tagator/index'

const components = [
  QBox,
  QButton,
  WRow,
  WCol,
  WTag,
  WShowMore,
  WLimitTextArea,
  WAlert,
  QInput,
  QInputNumber,
  QPercent,
  QCheckbox,
  QRadio,
  QGuide,
  QCapsule,
  QModal,
  QSwitch,
  QCalendar,
  QTooltip,
  QPoptip,
  QSelect,
  QAside,
  QTab,
  QTransfer,
  QLineChart,
  QPieChart,
  QMapChart,
  QLingBarChart,
  QFengBarChart,
  QRingChart,
  QRadarChart,
  QSlider,
  QTagator
]

const install = function (Vue) {
  if (install.installed) return
  components.map(component => Vue.component(component.name, component))
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  QBox,
  QButton,
  WRow,
  WCol,
  WTag,
  WShowMore,
  WLimitTextArea,
  MetaInfo,
  WAlert,
  QInput,
  QInputNumber,
  QCheckbox,
  QPercent,
  QRadio,
  QGuide,
  QCapsule,
  QModal,
  QSwitch,
  QCalendar,
  QTooltip,
  QPoptip,
  QSelect,
  QAside,
  QTab,
  QTransfer,
  QLineChart,
  QPieChart,
  QMapChart,
  QLingBarChart,
  QFengBarChart,
  QRingChart,
  QRadarChart,
  QSlider,
  QTagator
}



