<template>
<div class="d-i-b">
  <div class="fLeft d-i-b" v-bind:style="{width: width}">
    <div class="ys-slider ">
      <p class="slider-line" ref="line" @click="sliderMove">
        <span class="slider-selected" v-bind:style="{width: slider.width}" ref="selected"></span>
        <a class="slider-btn" @mousedown="sliderDown">
          <i class="ys-icon icon-arrow-right"></i>
        </a>
      </p>
    </div>
  </div>
  <input style="width: 80px; margin-left: 30px;display:none"
         type="number"
         v-model.number="current"
         @input="changeValue"
         v-bind:max="range[1]"
         v-bind:min="range[0]">
  <span class="m-l-5" style="line-height:30px;">{{unit}}</span>
</div>  
</template>

<style>
  .ys-slider p.slider-line {
    padding: 0;
    margin: 10px auto;
    height: 6px;
    text-align: left;
    border-radius: 8px;
    background:#46578e;
  }
  .ys-slider .slider-selected {
    color: #fff;
    background: #00bd85;
    display: block;
    width: 80%;
    height: 6px;
    line-height: 6px;
    text-align: right;
    float: left;
    border-radius: 8px;
  }
  .ys-slider .slider-btn {
    background:linear-gradient(to bottom, #ffffff, #c1d4f1);
    opacity: 1;
    width: 12px;
    height: 12px;
    font-size: 12px \9;
    font-size: .75rem;
    line-height: 0;
    position: absolute;
    border:none;
    border-radius: 1.5em;
    margin-top: -0.25em;
    text-align: center;
    margin-left: -0.5em;
    display: inline-block;
    float: left;
  }
  .ys-slider .slider-line:hover .slider-btn{
    opacity: 1;
  }
  .ys-slider .slider-btn i{
    transform: scale(1,1);
    margin-left:0.5px;
  }
</style>

<script>
  export default {
    name:"QSlider",
    props: {
      value: {
        default: 0
      },
      range: {
        default: [0,100]
      },
      width: {
        default: '400px'
      },
      unit:{
        default:""
      }
    },
    data () {
      return {
        slider: {
          width: '0%',
          _range: [],
        },
        current:""
      }
    },
    mounted () {
      this.current=this.value;
      this.slider._range = this._range(this.range)
      $(document).on("mouseup","body", function () {
          $(document).off("mousemove")
        })
      this.slider.ml = $(this.$refs.line).offset().left
      this.slider.all_width = $(this.$refs.line).width()
    },

    methods: {
      _range (range) {
        let l = []
        for(let i =0; i<range[1]; i++){
          if(i>range[0]){
            l.push(i)
          }
        }
        return l
      },
      sliderDown(e){
        $(document).on("mousemove",e, this.sliderMove)
      },
      sliderMove(e){
        e = event || e
        let mouseX = e.pageX
        let p = (mouseX - this.slider.ml) / this.slider.all_width * 100
        if(p >= 100){
          this.slider.width = '100%'
          this.current = this.range[1]
          return
        }
        if(p <= 0){
          this.slider.width = '0%'
          this.current = this.range[0]
          return
        }

        this.slider.width = p + '%'
        if(p == 100){
          this.current = this.range[1]
          return
        }
        let a = parseInt(p / (100 / this.slider._range.length))
        this.current = this.slider._range[a]
      },
      changeValue(){
        this.current = parseInt(this.current)
        if(this.current >= this.range[1]){
          this.slider.width = '100%';
          this.current=this.range[1];
        }else if(this.current <= this.range[0]){
          this.slider.width = '0%'
          this.current=this.range[0];
        }else{
          let index = this.slider._range.indexOf(parseInt(this.current))
          this.slider.width = (index / this.slider._range.length * 100) + '%'
        }
      }
    },
    components: {},

    watch: {
      current: function () {
        this.changeValue()
        this.$emit('update:value',this.current);
      },
      value: function () {
        this.current=this.value;
      },
    }
  }
</script>


